import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getTags } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { formatDistanceToNow } from 'date-fns';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { TagActions } from './components/TagActions';
import './tags.scss';

const Tags = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getTags((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddTag = (newTag) => {    
    setData(prevTags => [newTag, ...prevTags]);
  };
  
  const handleEditTag = (editedTag) => {    
    const edit = (arr, item) => {
      return arr.map(tag => {        
        if(tag.id === item.id) {
          tag = item;
        }
        return tag;
      });
    }
    setData(prevTags => edit(prevTags, editedTag))
  }
  
  const handleDeleteTag = (tagID) => {
    setData(prevTags => prevTags.filter(tag => {
      return tag.id !== tagID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Tags <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <TagActions 
                  showAdd
                  addCallback={handleAddTag}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "name",
                        accessor: "name",
                        Cell: row => <Link to={`/tags/${row.original.id}`}>
                          {row.value}
                        </Link>
                      },
                      {
                        Header: "category",
                        accessor: "categoryId",
                        Cell: ({ value, original: { category = {}}}) => <Link to={`/categories/${value}`}>
                          {category.name}
                        </Link>
                      },
                    ]
                  },
                  {
                    Header: "Actions",
                    columns: [
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <TagActions 
                            showDelete 
                            showEdit 
                            tag={original} 
                            deleteCallback={handleDeleteTag}
                            editCallback={handleEditTag}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Tags;
