import React, { useState, useEffect } from 'react';
import { Form, Field } from 'react-final-form';
import { RenderInput, RenderSelect, ErrorToast } from '../../../components';
import { toSelectOptions } from '../../../handlers/Utils';
import { getCategories } from '../../../handlers/Api/Calls';

export const TagForm = ({
    tag = {},
    onSubmit = values => console.log(values),
    isAdd = false,
    children
}) => {
    const [data, setData] = useState([]);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(true);

    const validate = (fields) => {
        const errors = {};
        // if(!fields["phone"]) errors["phone"] = 'required';
        return errors;
    }

    useEffect(() => {
        getCategories((apiLoading, apiData, apiError) => {
            setData(apiData);
            setError(apiError);
            setIsLoading(apiLoading);
        });
    }, []);

    return (
        <div className="tag-form">
            {error && <ErrorToast error={error} />}
            <Form
                onSubmit={onSubmit}
                validate={validate}
                initialValues={tag}
                render={({ handleSubmit }) => (
                    <form onSubmit={handleSubmit}>
                        <Field 
                            name="name" 
                            label="name"
                            component={RenderInput}
                            placeholder="name" 
                        />
                        <Field 
                            name="categoryId" 
                            label="Category"
                            component={RenderSelect}
                            options={toSelectOptions(data)}
                            placeholder="category" 
                        />
                        {children}
                    </form>
                )}
            />
        </div>
    )
}
